import { stopSubmit } from "redux-form";
import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
//const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';

const DELETE_POST = 'DELETE_POST';

let initialState = {
    posts: [
        {id: 1, message: 'Hi, how are you?', count: 15},
        {id: 2, message: 'It is my first post!', count: 20}
      ],
  //  newPostText: '',
    profile: null,
    status: ''
}

const profileReducer = (state = initialState, action) => {
    switch(action.type) {
        case ADD_POST:  {
            let newPost = {
                id: 3, 
                message: action.newPostText, 
                count: 0
              }; 
             return /*let stateCopy =*/ {
                  ...state,
                  newPostText: '',
                  posts: [...state.posts, newPost]
                };
              //stateCopy.posts = [...state.posts];
              //stateCopy.posts.push(newPost); 
              //stateCopy.newPostText = '';
               // return stateCopy;     
              //state.posts.push(newPost);
              //state.newPostText = '';
            //return state;
            }
            case DELETE_POST: {
              return {...state, posts: state.posts.filter(p => p.id !== action.postId)}
            }

       /* case UPDATE_NEW_POST_TEXT: {
          return {
               ...state,
               newPostText: action.newText
              }; 
           // stateCopy.newPostText = action.newText;
           // return stateCopy;
          } */
        case SET_USER_PROFILE: {
          return {
              ...state,
              profile: action.profile
            };
          }
        case SET_STATUS: {
          return {
              ...state,
              status: action.status
            };
          }
        case SAVE_PHOTO_SUCCESS: {
           return {
               ...state,
               profile: {...state.profile, photos: action.photos}     //в разделе фото поменяй нам фото
           }
        }
        default:                //вместо break, чтобы case не проваливался в следующий case
            return state;      //если придет action, которого нет
        
    }    
    
    /*if (action.type === ADD_POST) {
        let newPost = {
          id: 3, 
          message: state.newPostText, 
          count: 0
        };      
        state.posts.push(newPost);
        state.newPostText = '';        
      } else if (action.type === UPDATE_NEW_POST_TEXT) {
        state.newPostText = action.newText;                             
      }

      return state;*/
}

export const addPostActionCreator = (newPostText) => {
    return {
        type: ADD_POST,
        newPostText        
    };
  }

/*export const updateNewPostTextActionCreator = (text) => {
    return {
        type: UPDATE_NEW_POST_TEXT,
        newText: text
    };
  }*/

export const deletePost = (postId) => {
    return {
        type: DELETE_POST,
        postId       
    }
  };

export const savePhotoSuccess = (photos) => {
  return { 
    type: SAVE_PHOTO_SUCCESS,
    photos 
  }
};

export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile });

export const getUserProfile = (userId) => {

  return async (dispatch) => {

   let response = await usersAPI.getProfile(userId);                                           
        dispatch(setUserProfile(response.data));                  
      }
};

export const savePhoto = (file) => {                                  //загрузить фото с компьютера

  return async (dispatch) => {

   let response = await profileAPI.savePhoto(file);                                           
        dispatch(savePhotoSuccess(response.data.data.photos));                  
      }
};

export const setStatus = (status) => ({ type: SET_STATUS, status });   // добавить пост

export const getStatus = (userId) => {                                 //получить пост с сервера

  return (dispatch) => {

    profileAPI.getStatus(userId)
      .then(response => {                                     
            dispatch(setStatus(response.data));                  
        });
  }
};

export const updateStatus = (status) => {

  return (dispatch) => {

    profileAPI.updateStatus(status)
      .then(response => { 
         if (response.data.resultCode === 0) {                                   
            dispatch(setStatus(status)); 
          }                 
      });      
  }
};

export const saveProfile = (profile) => {                                  //загрузить фото с компьютера

  return async (dispatch, getState) => {
   const userId = getState().auth.userId;                    //из глобального state получаем userId

   let response = await profileAPI.saveProfile(profile); 
   
      if(response.data.resultCode === 0) {
        dispatch(getUserProfile(userId));                   //используем другой reducer
      } else {
        dispatch(stopSubmit('edit-profile', {_error: response.data.messages[0]}));
        return Promise.reject(response.data.messages[0]);
      }                
    }
};


export default profileReducer;