import React, { useState } from 'react';
import Preloader from '../../common/preloader/preloader';
import s from './profileInfo.module.css';
//import ProfileStatus from './profileStatus';
import ava from '../../../assets/images/ava.jpg';
import photoProfile from '../../../assets/images/photoProfile.jpg';
import ProfileStatusWithHooks from './profileStatusWithHooks';
import ProfileDataForm from './profileDataForm';

const ProfileInfo = ({profile, status, updateStatus, isOwner, savePhoto, saveProfile}) => {

    let [editMode, setEditMode] = useState(false);

    if (!profile) {         // ! это eсли null или undefined
        return <Preloader />
    };    

    const onMainPhotoSelected = (e) => {
       if(e.target.files.length) {
          savePhoto(e.target.files[0])
       }
    }

    const onSubmit = (formData) => {
       saveProfile(formData).then(
        () => {
            setEditMode(false);                  //чтобы работал режим редактирования, когда thunk удачно dispatch 
        }
       )       
   }
 

    return (
        <div>
          <div className={s.image}>
              <img src={photoProfile} alt='' />                               
          </div>
          <div className={s.info}>  
              <div className={s.avatar}>        
                  <img src={profile?.photos?.large || ava} alt=''/> 
                  {isOwner && <input type={'file'} className={s.avaLoad} onChange={onMainPhotoSelected}/>}   
              </div>  
             
             { editMode
               ? <ProfileDataForm initialValues={profile} profile={profile} onSubmit={onSubmit}/> 
               : <ProfileData goToEditMode={() => {setEditMode(true)}} profile={profile} isOwner={isOwner}/> }   {/*если режим редактирования то...*/}              

              <ProfileStatusWithHooks status={status} updateStatus={updateStatus}/>         
        </div>          
    </div>         
    );
}

const Contacts = ({contactTitle, contactValue}) => {
    return <div className={s.contacts}><b>{contactTitle}</b>: {contactValue}</div>
}

const ProfileData = ({profile, isOwner, goToEditMode}) => {
console.log(profile);
    return  <div>
    { isOwner && <div><button onClick={goToEditMode}>Edit</button></div> }
    <div>
        <b>Full Name</b>: {profile.fullName}
    </div>
    <div>
        <b>Looking For A Job</b>: {profile.lookingForAJob ? 'yes' : 'no'}
    </div>
    { profile.lookingForAJob &&
      <div>
         <b>My Professional Skills</b>: {profile.lookingForAJobDescription}
      </div>
    }
    <div>
        <b>About Me</b>: {profile.aboutMe}
    </div>                  
    <div>
        <b>Contacts</b>: {Object.keys(profile.contacts).map(key => {
           return <Contacts key={key} contactTitle={key} contactValue={profile.contacts[key]}/>
        })}
    </div>               
</div>
}

export default ProfileInfo;